.name {
  grid-area: n;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.1;
  margin-top: 0;
}

.year {
  grid-area: y;
  justify-self: end;
}

.time {
  grid-area: t;
  justify-self: end;
}

.country {
  grid-area: c;
  font-size: 14px;
}

.genre {
  grid-area: g;
  font-size: 12px;
}

.img {
  grid-area: im;
  width: 100%;
  height: auto;
}

.rating {
  grid-area: r;
  font-size: 14px;
  font-weight: 700;
}

.movieCard {
  display: inline-grid;
  background-color: #cccccc;
  width: 200px;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto 20px 50px 15px 20px;
  gap: 5px;
  grid-template-areas: 
    "im im"
    "r y"
    "n n"
    "c t"
    "g g";

  *:not(.img) {
    padding: 0 10px;
  }
}
