$baseSize: 64px;

.wrapper {
  margin: 20px auto;
  display: flex;
  flex-flow: row nowrap;
  width: 512px;
  height: $baseSize;
  background-color: rgb(161, 202, 230);
  border-bottom: 2px solid rgb(120, 120, 120);
  border-radius: $baseSize / 2;
}

.inputText {
  width: 100%;
  border: none;
  background-color: transparent;
  padding: 10px;
  padding-left: 20px;
  outline: 1px solid transparent;
  font-size: 24px;
}

.btn {
  position: relative;
  width: $baseSize;
  height: $baseSize;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  background-color: transparent;
  transition: 0.3s all ease-in-out;
  filter: drop-shadow(0 0 3px #555555);

  &:active {
    filter: brightness(0.9);
  }

  span {
    position: absolute;
    top: 24px;
    left: 10px;
    display: block;
    width: 38px;
    height: 18px;
    background-color: transparent;
    border-radius: 8px;
    transform: rotateZ(52deg);
    transition: 0.3s all ease-in-out;
  }

  span:before,
  span:after {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    width: 20px;
    height: 8px;
    background-color: #ffffff;
    border-radius: 8px;
    transform: rotateZ(0);
  }
}

.circle {
  position: relative;
  top: -6px;
  left: 0;
  width: 33px;
  height: 33px;
  border: 8px solid #ffffff;
  border-radius: 50%;
  background-color: transparent;
  transition: 0.3s all ease-in-out;
}
